import React from "react"
import { graphql, useStaticQuery } from "gatsby"
import BackgroundImage from "gatsby-background-image"
import Header from "../components/header"

const SectionHero = ({ children, className }) => {
  const data = useStaticQuery(
    graphql`
      query {
        desktop: file(
          relativePath: { eq: "hero-image.png" }
        ) {
          childImageSharp {
            fluid(quality: 90, maxWidth: 1920) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }

        imgLeft: file(relativePath: { eq: "kodim-white.png" }) {
          childImageSharp {
            fluid(maxWidth: 700) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }

        imgRight: file(relativePath: { eq: "thoughtful_indonesia_white.png" }) {
          childImageSharp {
            fluid(maxWidth: 700) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    `
  )

  // Set ImageData.
  const imageData = data.desktop.childImageSharp.fluid
  const imageLeft = data.imgLeft.childImageSharp.fluid
  const imageRight = data.imgRight.childImageSharp.fluid

  return (
    <BackgroundImage
      Tag="section"
      className={`bg-gray-700`}
      fluid={imageData}
      // backgroundColor={`#040e18`}
      style={{
        backgroundPosition: "center",
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover",
        height: "720px",
        width: "100%",
      }}
    >
      <div className="flex flex-col min-h-screen w-full px-24 items-end justify-center">
        {children}
      </div>
    </BackgroundImage>
  )
}

export default SectionHero
