import React from "react"
import BackgroundImage from 'gatsby-background-image'
import { Link } from "gatsby"

const ListTour = ({ tours }) => (

    <div class="flex flex-wrap -mx-1 lg:-mx-4">
        {tours && tours.map(({ node }) => {

            let tour = node.frontmatter

            return (

                <div class="w-full md:w-1/2 lg:my-4 lg:px-4 lg:w-1/3 mb-5">

                    <div className="xtooltip transition delay-700 duration-300 ease-in-out">
                        {/* <a href={tour.url} target="_blank" rel="noreferrer" className="text-gray-100 hover:shadow-xl hover:underline"> */}
                        <Link to={node.fields.slug}>
                            <BackgroundImage
                                Tag="section"
                                className={`w-full h-96 rounded-lg overflow-hidden hover:shadow-lg transition duration-500`}
                                // style={{
                                //     backgroundPosition: "center",
                                //     backgroundRepeat: "no-repeat",
                                //     backgroundSize: "cover",
                                //     minHeight: "100vh",
                                //     width: "100%",
                                // }}
                                fluid={tour.thumbnailImage.childImageSharp.fluid}
                            >

                            </BackgroundImage>
                            <div className="body-text py-4">
                                <div className="font-bold leading-tight text-2xl text-black">
                                    {tour.title}
                                </div>
                                <hr className="my-4"/>
                                <button type="button" className="inline-flex items-center rounded-md border border-transparent text-sm font-medium leading-4 hover:text-indigo-700">
                                    Jelajahi
                                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-6 h-6">
                                        <path strokeLinecap="round" strokeLinejoin="round" d="M4.5 19.5l15-15m0 0H8.25m11.25 0v11.25" />
                                    </svg>

                                </button>
                            </div>
                        </Link>


                    </div>

                </div>

            )

        })}
    </div>

)

export default ListTour